<template>
  <div class="login_container">
    <div class="login_main">
      <h1>欢迎使用人力资源管理系统</h1>
      <a-form
        class="login_form"
        :model="formState"
        name="basic"
        :wrapper-col="{ span: 24 }"
        autocomplete="off"
        @finish="onFinish"
        @finishFailed="onFinishFailed"
      >
        <a-form-item
          name="empCode"
          :rules="[{ required: true, message: '请输入员工号!' }]"
        >
          <a-input
            v-model:value="formState.empCode"
            size="large"
            placeholder="请输入用户名"
          />
        </a-form-item>

        <a-form-item
          name="password"
          :rules="[{ required: true, message: '请输入密码!' }]"
        >
          <a-input-password
            v-model:value="formState.password"
            size="large"
            placeholder="请输入密码"
          />
        </a-form-item>

        <a-form-item>
          <a-button
            type="primary"
            html-type="submit"
            size="large"
            :style="{ width: '100%' }"
            >登录</a-button
          >
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive } from "vue";

import employeeService from "@/service/employeeService";
import { message } from "ant-design-vue";
import { useRouter } from "vue-router";
import { setUserInfo } from "@/utils/authUtil";

export default defineComponent({
  setup() {
    const router = useRouter();
    const formState = reactive({
      empCode: undefined,
      password: undefined,
    });

    const onFinish = async (values) => {
      try {
        const ret = await employeeService.login(values);
        if (!ret) {
          message.info("用户名或者密码不正确");
        } else {
          message.info("登录成功");
          setUserInfo(ret);
          router.push("/");
        }
      } catch (error) {
        message.info("登录失败" + error);
      }
    };

    const onFinishFailed = (errorInfo) => {
      console.log("Failed:", errorInfo);
    };
    return {
      formState,
      onFinish,
      onFinishFailed,
    };
  },
});
</script>

<style lang="scss">
.login_container {
  height: 100vh;
  /*弹性布局居中*/
  display: flex;
  justify-content: center;
  align-items: center;
  /*渐变背景*/
  background: linear-gradient(200deg, #e3c5eb, #a9c1ed);
  /*溢出影藏*/
  overflow: hidden;
}

.login_main {
  box-sizing: border-box;
  width: 400px;
  height: 450px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 10px 5px 5px rgb(0 0 0 / 20%);
  h1 {
    height: 150px;
    line-height: 150px;
    text-align: center;
  }
}

.login_form {
  padding: 0 50px;
}
</style>
