import { request } from "./request/request";

class EmployeeService {
  add(data) {
    return request("/api/employee/add", {
      method: "POST",
      data,
    });
  }
  update(data) {
    return request("/api/employee/update", {
      method: "POST",
      data,
    });
  }
  delete(data) {
    return request("/api/employee/delete", {
      method: "POST",
      data,
    });
  }
  list() {
    return request("/api/employee/list", {
      method: "POST",
    });
  }
  listAll() {
    return request("/api/employee/listAll", {
      method: "POST",
    });
  }
  queryByCode(data) {
    return request("/api/employee/queryByCode", {
      method: "POST",
      data,
    });
  }
  likeByCode(data) {
    return request("/api/employee/likeByCode", {
      method: "POST",
      data,
    });
  }

  login(data) {
    return request("/api/employee/login", {
      method: "POST",
      data,
    });
  }

  modifyPwd(data) {
    return request("/api/employee/modifyPwd", {
      method: "POST",
      data,
    });
  }

  
}

export default new EmployeeService();
